* {
  box-sizing: border-box;
  font-family: "Permanent Marker", cursive;
}

.searchBar {
  display: flex;
}

.Bar fieldset {
  background: #ffeecad8;
  border: none;
  height: 50px;
  border-radius: 20px;
  width: 55vw;
}

.Bar fieldset input {
  border: none;
  background: none;
  outline: none;
  padding-top: 4px;
  font-size: 18px;
  width: 45vw;
}

.guess-round- {
  background: #ffeecad8;
  border-radius: 20px;
  padding: 0px 10px 0px 10px;
  width: 50vw;
  font-size: 20px;
}

.Button button {
  font-size: 18px;
  cursor: pointer;
  border: none;
  background: #ffeecad8;
  text-align: center;
  height: 50px;
  border-radius: 20px;
}
